html {
    font-family: "Open Sans";
    font-size: 16px;
}

@font-face {
    font-family: 'Open Sans';
    src: url('./fonts/open-sans.woff2') format('woff2'), url('./fonts/open-sans.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

body {
    background-color: rgba(96, 102, 208, 0.8);
}

.quiz-container {
    background: #FFFFFF;
    border-radius: 24px;
    padding: 25px;
    margin: 5rem;
}

.btn-container {
    display: flex;
    flex-direction: column;
}

.btn-country {
    background: #FFFFFF;
    border: 2px solid rgba(96, 102, 208, 0.7);
    box-sizing: border-box;
    border-radius: 12px;
    padding-top: 15px;
    padding-bottom: 15px;
    margin-top: 25px;
    color: rgba(96, 102, 208, 0.8);
    cursor: pointer;
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
}

.btn-country:hover {
    color: #FFFFFF;
    background-color: #F9A826;
    outline: none;
    transition: 0.1s;
}

.question-quiz {
    font-weight: bold;
    font-size: 24px;
    line-height: 36px;
    color: #2F527B;
    margin: 0;
}

em {
    color: green;
    font-style: normal;
}

.correct {
    background-color: green;
}

.flag {
    width: 84px;
    height: 54px;
}